@use "global" as *;


.flex-section {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: rem(63);
}

.flex-section__img {
  flex-grow: 1;
  aspect-ratio: 1 / 1;
  max-width: rem(508);
  width: rem(508);
  height: rem(508);
  // width: 100%;
  position: relative;
}

.flex-section__img img {
  aspect-ratio: 1 / 1;
  max-width: rem(468);
  // width: 100%;
  width: rem(468);
  object-fit: cover;
  position: absolute;
  top: 0;
  right: 0;
  z-index: 30;
}

.flex-section__img::after {  // 影の色
  content: "";
  aspect-ratio: 1 / 1;
  width: rem(468);
  height: rem(468);
  max-width: rem(468);
  background: rgba(238, 213, 206, 0.40);
  position: absolute;
  bottom: 0;
  left: 0;
  z-index: 10;
}

.flex-section__texts {

}

.flex-section__title {

}

.flex-section__text {
  margin-top: rem(48);
}
