@use "global" as *;


/* ブロック要素（パンくず全体） */
.breadcrumb {
  display: flex;
  flex-wrap: wrap;
  list-style: none;
  color: #6E6E6E;
  leading-trim: both;
  text-edge: cap;
  font-size: rem(14);
}

.breadcrumb span {  // プラグイン『Breadcrumb NavXT』でデフォルトで生成される要素に指定。
  margin-right: rem(12);  // パンくずアイテム間のスペース
}

.breadcrumb > span:first-of-type {
  color: #1C1C1C;
  padding-left: rem(16);
  position: relative;
}

.breadcrumb > span:first-of-type::before {  // 最初の要素に、擬似要素で『家の形のアイコン』を付与
  content: "";
  width: rem(12);
  height: rem(12);
  background: url("../images/common/breadcrumb-home-icon.svg") no-repeat center center / contain;
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
}

.breadcrumb > span:first-of-type::after {  // 最初の要素に、擬似要素で『下線』を付与
  content: "";
  width: rem(28);
  height: 1px;
  background: #1C1C1C;
  position: absolute;
  bottom: rem(3);
  right: 0;
}

.breadcrumb span:last-child {
  margin-right: 0;  // 最後のアイテムにはスペースを適用しない
}

.breadcrumb span a {
  transition: all 0.2s ease-out;
}

.breadcrumb span a:hover {  // hover
  opacity: 0.6;
}

.breadcrumb span span {
  margin-right: 0;
}

.breadcrumb__separator {  // プラグイン『Breadcrumb NavXT』を、管理画面から設定する際に、『一般』→『パンくずの区切り』の部分に、『<span class="breadcrumb__separator"></span>』を指定して、このHTML形式で生成されるように指定。
  width: rem(4);
  position: relative;
}

.breadcrumb__separator::after {  // 『>』マークを擬似要素で作成
  content: "";
  width: rem(6);
  height: rem(6);
  border-top: 2px solid #BBB;
  border-right: 2px solid #BBB;
  position: absolute;
  top: 50%;
  left: calc(50% - rem(1));
  transform: translate(-50%, -50%) rotate(45deg);
}
