@use "global" as *;


.title03 {

}

.title03__en {
  display: inline-block;
  color: #1C1C1C;
  leading-trim: both;
  text-edge: cap;
  font-family: $font-family-en-1;
  font-size: rem(16);
  font-weight: $bold;
  line-height: 1.4;
  letter-spacing: 0.04em;
  text-indent: 0.04em;
  text-transform: capitalize;
  position: relative;
}

.title03__en::after {  // 右側の線
  content: "";
  width: rem(40);
  height: 1px;
  background: #1C1C1C;
  position: absolute;
  left: calc(100% + rem(16));
  top: 50%;
  transform: translateY(-50%);
}

.title03__ja {
  margin-top: rem(25);
  color: #1C1C1C;
  leading-trim: both;
  text-edge: cap;
  font-family: $font-family-ja-1;
  font-size: rem(31);
  font-weight: $bold;
  line-height: 1.4;
}
